<!-- 条形图 -->
<template>
  <div :id="id" class="charts"></div>
</template>

<script>
import $echarts from 'echarts'
export default {
  name: 'column-chart',
  props: {
    chartData: {
      type: Object,
      default: () => {}
    },
    id: {
      type: String,
      default: 'charts'
    }
  },
  watch: {
    chartData: {
      handler: function(newVal, oldval) {
        this.chartData = newVal
        this.init()
      },
      deep: true
    }
  },
  methods: {
    init() {
      let _this = this
      const column = $echarts.init(document.getElementById(this.id))
      column.setOption(
        {
          textStyle: {
            fontSize: 14
          },
          title: {
            text: this.chartData.title,
            left: '5%',
            textStyle: {
              color: '#62523a',
              fontSize: 16
            }
          },
          legend: {
            data: this.chartData.legend,
            itemWidth: 10,
            itemHeight: 10,
            itemGap: 60,
            textStyle: {
              color: '#746651'
            }
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
            },
            formatter: function(data) {
              let unit = _this.chartData.type === 2 ? '%' : _this.chartData.type === 1 ? '人' : '' //单位
              let color = [
                'linear-gradient(0deg,#5cf2c6 0%, #8cffb8 100%,#56ecc3 100%, #20d8cd 100%)',
                'linear-gradient(180deg, #fde18d 0%,#ff8676 100%)'
              ] //签约人数color
              let colorRate = [
                'linear-gradient(0deg,#72c6f2 0%, #befaff 100%);',
                'linear-gradient(180deg, #ffea8b 0%, #ffc376 100%)'
              ] //签约率color
              let currentcolor = _this.chartData.type === 1 ? color : colorRate
              let result = '' + data[0].name + '<br/>'
              for (let i = 0; i < data.length; i++) {
                let marker =
                  '<span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-image:' +
                  currentcolor[i] +
                  ';"></span>'
                result += marker + data[i].seriesName + '：' + data[i].value + ' ' + unit + '<br/>'
              }
              return result
            }
          },
          grid: {
            left: '5%',
            right: '5%',
            bottom: '5%',
            containLabel: true
          },
          xAxis: [
            {
              type: 'category',
              data: this.chartData.xAxis,
              axisPointer: {
                type: 'shadow'
              },
              axisLine: {
                lineStyle: {
                  color: '#f5efea'
                }
              },
              axisLabel: {
                color: '#746651',
                interval: 0,
                textStyle: {
                  fontSize: 14 //更改坐标轴文字大小
                }
              }
            }
          ],
          yAxis: [
            {
              type: 'value',
              nameGap: 30,
              min: this.chartData.type === 2 ? 0 : null,
              max: this.chartData.type === 2 ? 100 : null,
              splitNumber: 5,
              axisLine: {
                lineStyle: {
                  color: '#f5efea'
                }
              },
              splitLine: {
                lineStyle: {
                  color: '#f5efea'
                }
              },
              axisLabel: {
                color: '#746651',
                formatter: this.chartData.type === 2 ? '{value} %' : '{value}',
                textStyle: {
                  fontSize: 14 //更改坐标轴文字大小
                }
              }
            }
          ],
          series: this.formatSeries()
        },
        true
      )
      window.addEventListener('resize', function() {
        column.resize()
      })
    },
    //处理series的值
    formatSeries() {
      let _this = this
      let arr = []
      if (this.chartData.series.length > 0) {
        let colorList = []
        if (this.chartData.type === 2) {
          colorList = [['#72c6f2', '#befaff'], ['#ffea8b', '#ffc376']]
        } else {
          colorList = [['#5cf2c6', '#8cffb8'], ['#fde18d', '#ff8676']]
        }
        this.chartData.series.forEach((item, index) => {
          let obj = {}
          obj.name = this.chartData.legend[index]
          obj.type = 'bar'
          obj.data = item
          obj.barWidth = 20
          obj.label = {}
          obj.label.normal = {}
          obj.label.normal.show = true
          obj.label.normal.position = 'top'
          obj.itemStyle = {}
          obj.itemStyle.normal = {}
          obj.itemStyle.normal.color = function() {
            return {
              type: 'linear',
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [
                { offset: 0, color: colorList[index][0] },
                { offset: 1, color: colorList[index][1] }
              ]
            }
          }
          arr.push(obj)
        })
      }
      return arr
    }
  }
}
</script>

<style lang="less" scoped>
.charts {
  width: 100%;
  height: 400px;
}
</style>
