<!-- 机构统计 -->
<template>
<div class="list-common-box flex-column">
  <!-- 标题 -->
  <div class="title-box">
    <Breadcrumb :isLevel='2'></Breadcrumb>
  </div>
  <!-- 大盒子 -->
  <div class="count-box flex-overflow">
  <!-- 上 -->
    <div class="count-top-box">
      <!-- 主要数据 -->
      <div class="card-box">
        <!-- 搜索 -->
        <div class="search-bar">
          <!-- 左 -->
          <div class="search-left">
            <div class="search-item">
              <span class="search-label">统计时间</span>
              <el-date-picker v-model="date" type="daterange" align="left" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions" @change="handleDatechange" value-format="yyyy-MM-dd" clearable></el-date-picker>
            </div>
            <div class="search-item">
              <span class="search-label">所属机构</span>
              <treeselect :multiple="false" :options="OrganOptions"  placeholder="请选择机构" v-model="searchKey.organizationId" />
            </div>
          </div>
          <div class="search-right">
            <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
            <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
          </div>
        </div>
        <!-- 表格 -->
        <div class="table-box">
          <el-table :data="dataList" tooltip-effect="dark" stripe> 
            <el-table-column label="序号" align="center" width="80">
              <template slot-scope="scope">
                {{scope.$index+(pageNum - 1) * pageSize + 1}}
              </template>
            </el-table-column>
             <el-table-column prop="teamName" label="团队" align="center">
              <template slot-scope="scope">
                {{scope.row.teamName?scope.row.teamName:EMPTYTEXT}}
              </template>
            </el-table-column>
            <el-table-column prop="registerOrganization" label="所属机构" align="center">
              <template slot-scope="scope">
                {{scope.row.registerOrganization?scope.row.registerOrganization:EMPTYTEXT}}
              </template>
            </el-table-column>
            <el-table-column prop="actualTotal" label="实际签约人数"  align="center">
              <template slot-scope="scope">                  
                {{scope.row.actualTotal!==''?scope.row.actualTotal:EMPTYTEXT}}
              </template>
            </el-table-column>
             <el-table-column prop="actualTargetSignRate" label="签约率（%）"  align="center">
              <template slot-scope="scope">                  
                {{scope.row.actualTargetSignRate!==''?scope.row.actualTargetSignRate+'%':EMPTYTEXT}}
              </template>
            </el-table-column>
            <el-table-column prop="actualAppoint" label="履约人数"  align="center">
              <template slot-scope="scope">                  
                {{scope.row.actualAppoint!==''?scope.row.actualAppoint:EMPTYTEXT}}
              </template>
            </el-table-column>
            <el-table-column prop="actualAppointTime" label="履约人次" align="center">
              <template slot-scope="scope">
                {{scope.row.actualAppointTime!==''?scope.row.actualAppointTime:EMPTYTEXT}}
              </template>
            </el-table-column>
             <el-table-column prop="appointRate" label="履约率（%）"  align="center">
              <template slot-scope="scope">                  
                {{scope.row.appointRate!==''?scope.row.appointRate+'%':EMPTYTEXT}}
              </template>
            </el-table-column>
          </el-table>
        </div>
        <!-- 分页 -->
        <div class="page-box">
          <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" :pageNum="pageSize" @updateList="changePage" ref="paginationEvent"/>
        </div>
      </div>
    </div>
    <!-- 下 -->
    <div class="count-bottom-box" v-show="dataList.length>0">
      <h3>数据对比</h3>
      <!-- 签约人数统计 -->
      <div class="echart-box">
        <ColumnChart :id="'teamSignChart'" :chartData="signData" v-show="signData"></ColumnChart>
      </div>
      <!-- 签约率履约率统计 -->
      <ColumnChart :id="'teamSignRateChart'" :chartData="signRateData" v-show="signRateData"></ColumnChart>
    </div>
  </div>
</div>

</template>

<script>
  import dateConfig from "../../util/dateConfig.js" //日期选择配置项
  import Treeselect from '@riophae/vue-treeselect' //树型select
  import '@riophae/vue-treeselect/dist/vue-treeselect.css'
  import ColumnChart from "./column-chart" //条形图
  export default {
    name: 'team-count',
    components: {
      Treeselect,
      ColumnChart
    },
    data() {
      return {
        date:'',//时间
        searchKey:{
          startDate:'', //开始时间
          endDate:'',//结束时间
          organizationId:null,//所属机构id
        },
        pickerOptions:dateConfig, //日期选择便捷选项
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList:[],//团队列表
        OrganOptions:[],//所属机构下拉
        signData:null, //签约图表
        signRateData:null, //签约率图表
        signDataObj:{
          title:'',
          legend:[],
          xAxis:[],
          series:[],
        }, //签约图表
        signRateDataObj:{
          title:'',
          legend:[],
          xAxis:[],
          series:[],
        } //签约率图表
      }
    },
    created() {
      this.getOrganList() //获取所属机构列表
      this.getDatalist() //获取团队统计列表
    },
    methods: {
      //日期发生改变
      handleDatechange(val) {
        if (val && val.length > 0) {
          this.searchKey.startDate = val[0];
          this.searchKey.endDate = val[1];
        }else{
          this.searchKey.startDate = ''
          this.searchKey.endDate = ''
        }
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        this.date=''
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.searchKey.organizationId = null
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取机构列表
      getOrganList(){
        this.$http.post(process.env.VUE_APP_API+'/service/organization/getOrganization').then(res => {
          if(res.code===200){
          let initArrays = JSON.parse(JSON.stringify(res.data).replace(/organizationId/g, 'id').replace(/name/g, 'label').replace(/childrens/g, 'children'))
            this.OrganOptions = initArrays
          }else{
            this.OrganOptions = []
            this.$message.error(res.msg);
          }
        })
        .catch(error => {
          this.$message.error(error);
        });
      },
      //获取团队统计列表
      getDatalist(query) {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/team/getStatDtoByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //团队列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
            //图表数据
            if(this.dataList.length>0){
              this.signData = Object.assign({},this.signDataObj)
              this.signRateData = Object.assign({},this.signRateDataObj)
              let xAxis = []
              let totalTarget = [] //目标签约人数
              let actualTotal = [] //实际签约人数
              let actualTargetSignRate = [] //签约率
              let appointRate = [] //履约率
              this.dataList.forEach(item=>{
                xAxis.push(item.teamName||'')
                totalTarget.push(item.totalTarget)
                actualTotal.push(item.actualTotal)
                actualTargetSignRate.push(item.actualTargetSignRate)
                appointRate.push(item.appointRate)
              })
              this.signData.type=1
              this.signData.title = '签约人数统计'
              this.signData.legend = ['目标签约人数','实际签约人数']
              this.signData.xAxis = xAxis
              this.signData.series= [totalTarget,actualTotal]
              this.signRateData.type=2
              this.signRateData.title = '签约率/履约率统计'
              this.signRateData.legend = ['签约率','履约率']
              this.signRateData.xAxis = xAxis
              this.signRateData.series = [actualTargetSignRate,appointRate]
            }
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      }
    }
  }
</script>
<style lang='less'>
.count-box{
  .count-bottom-box{
    border-top:1px solid #f5efea;
    padding:20px;
    h3{
      font-size:16px;
      color: #62523a;
      margin-bottom:20px;
      font-weight:bold;
    }
    .echart-box{
      margin-bottom:20px;
    }
  }
}
</style>